.main-header {
    background-image: url('./1.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    min-height: 50vh;
    display: flex;
    align-items: center;
    border-radius: 15px;
    margin-bottom: 30px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    filter: brightness(70%);
}

.header-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* background: linear-gradient( 45deg, rgba(19, 12, 183, 0.85) 0%, rgba(82, 229, 231, 0.8) 100%); */
    display: flex;
    align-items: center;
}

.header-content {
    text-align: center;
    color: white;
    z-index: 2;
    position: relative;
    width: 100%;
}

.clinic-name {
    font-size: 3.5rem;
    margin-bottom: 15px;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
    font-weight: 700;
}

.clinic-subtitle {
    font-size: 1.5rem;
    margin-bottom: 10px;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
    opacity: 0.95;
}

.clinic-years {
    background: linear-gradient(45deg, #52E5E7 10%, #130CB7 100%);
    display: inline-block;
    padding: 10px 25px;
    border-radius: 25px;
    margin-top: 20px;
    font-size: 1.1rem;
    font-weight: 600;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.header-buttons {
    margin-top: 30px;
    display: flex;
    gap: 15px;
    justify-content: center;
    flex-wrap: wrap;
}

.cta-button {
    padding: 15px 30px;
    border: none;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
    display: inline-block;
}

.cta-button.primary {
    background: linear-gradient(45deg, #52E5E7 10%, #130CB7 100%);
    color: white;
    box-shadow: 0 4px 15px rgba(82, 229, 231, 0.4);
}

.cta-button.secondary {
    background: rgba(255, 255, 255, 0.2);
    color: white;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.3);
}

.cta-button:hover {
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(82, 229, 231, 0.6);
}


/* رسپانسیو */

@media (max-width: 768px) {
    .main-header {
        min-height: 60vh;
    }
    .clinic-name {
        font-size: 2.5rem;
    }
    .clinic-subtitle {
        font-size: 1.2rem;
    }
    .header-buttons {
        flex-direction: column;
        align-items: center;
    }
    .cta-button {
        width: 200px;
    }
}