* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background-color: #f5f7fa;
    color: #333;
    line-height: 1.6;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}     

:root {
  --primary-color: #130CB7;
  --primary-dark: #130CB7;
  --primary-light: #e6f7ff;
  --gradient: linear-gradient(45deg, #52E5E7 10%, #130CB7 100%);
}

header {
    background: linear-gradient(45deg, #52E5E7 10%, #130CB7 100%);
    color: white;
    padding: 30px 0;
    border-radius: 15px;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.clinic-name {
    font-size: 2.5rem;
    margin-bottom: 10px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.clinic-subtitle {
    font-size: 1.2rem;
    opacity: 0.9;
    margin-bottom: 5px;
}

.clinic-years {
    background-color: rgba(255, 255, 255, 0.2);
    display: inline-block;
    padding: 5px 15px;
    border-radius: 20px;
    margin-top: 10px;
    font-size: 0.9rem;
}

.status-bar {
    background-color: white;
    border-radius: 15px;
    padding: 20px 0;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.status-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.clinic-status {
    display: flex;
    align-items: center;
    gap: 10px;
}

.status-indicator {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #b42ecc;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.working-hours {
    color: #666;
}

.services {
    background-color: white;
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.services h2 {
    color: rgb(0, 139, 139);
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 2px solid #f0f0f0;
    font-size: 1.5rem;
}

.service-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    margin-top: 20px;
    background: linear-gradient(135deg, #f8fdff 0%, #e6f7ff 100%);
}

.service-card {
    background: #f9fbfd;
    border-radius: 10px;
    padding: 20px;
    border-right: 4px solid rgb(0, 139, 139);
    transition: transform 0.3s, box-shadow 0.3s;
}

.service-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.05);
}

.service-card h3 {
    color: var(--primary-dark);
    margin-bottom: 10px;
    font-size: 1.2rem;
}

.contact-section {
    margin-bottom: 30px;
}

.contact-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
}

.contact-card {
    background-color: white;
    border-radius: 15px;
    padding: 25px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    text-align: center;
}

.contact-card h2 {
    color: var(--primary-dark);
    margin-bottom: 20px;
    font-size: 1.5rem;
}

.phone-number {
    font-size: 1.8rem;
    color: rgb(0, 139, 139);
    margin: 15px 0;
    direction: ltr;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 20px;
    flex-wrap: wrap;
}

.social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: #f0f5ff;
    border-radius: 50%;
    color: rgb(0, 139, 139);
    font-size: 1.5rem;
    transition: all 0.3s;
    text-decoration: none;
}

.social-link:hover {
    background-color: var(--primary-color);
    color: white;
    transform: scale(1.1);

}

.doctors-slider {
    background-color: white;
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.doctors-slider h2 {
    color: var(--primary-dark);
    margin-bottom: 20px;
    font-size: 1.5rem;
}

.doctors-container {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    padding: 10px 5px;
}

.doctor-card {
    min-width: 200px;
    background: #f9fbfd;
    border-radius: 10px;
    padding: 20px;
    text-align: center;
    border-top: 4px solid rgb(0, 139, 139);
}

.doctor-name {
    font-weight: bold;
    margin: 10px 0 5px;
    color: rgb(0, 139, 139);
}

.doctor-specialty {
    color: #666;
    font-size: 0.9rem;
}

.doctor-experience {
    color: #888;
    font-size: 0.8rem;
    margin-top: 5px;
}

.appointment {
    background-color: white;
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.appointment h2 {
    color: var(--primary-dark);
    margin-bottom: 20px;
    font-size: 1.5rem;
}

.appointment-form {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 15px;
}

.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    color: rgb(0, 139, 139);
    font-weight: 500;
}

.form-group input,
.form-group select,
.form-group textarea {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    font-family: inherit;
}

.form-group textarea {
    resize: vertical;
    min-height: 80px;
}

.submit-btn {
    background: linear-gradient(45deg, #52E5E7 10%, #130CB7 100%);
    color: white;
    border: none;
    padding: 15px 30px;
    border-radius: 8px;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.3s;
    margin-top: 10px;
    grid-column: 1 / -1;
}

.submit-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(19, 12, 183, 0.3);
}

.map-container {
    background-color: white;
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.map-container h2 {
    color: var(--primary-dark);
    margin-bottom: 20px;
    font-size: 1.5rem;
}

.map-placeholder {
    height: 300px;
    background-color: #f0f5ff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--primary-color);
    font-size: 1.1rem;
    text-align: center;
}

footer {
    text-align: center;
    padding: 20px;
    color: #666;
    font-size: 0.9rem;
    margin-top: 30px;
    border-top: 1px solid #eee;
}

@media (max-width: 768px) {
    .clinic-name {
        font-size: 2rem;
    }
    .service-cards {
        grid-template-columns: 1fr;
    }
    .contact-cards {
        grid-template-columns: 1fr;
    }
    .phone-number {
        font-size: 1.5rem;
    }
    .status-content {
        flex-direction: column;
        gap: 10px;
        text-align: center;
    }
}